import ValueObject from '@/domain/base/ValueObject';
class Cell extends ValueObject {
    toString() {
        return this.getValue();
    }
    isIdentical(c) {
        if (this.getValue() !== c.getValue())
            return false;
        if (!this.getTooltip().is(c.getTooltip()))
            return false;
        return true;
    }
}
export default Cell;
