import { AntibiogramId, GramValues, IntegerNumberOfIsolates, Interval, OrganismValue, Place, Routes, SampleInfo, SensitivityValue, Settings, SingleAntibioticValue, Sources, } from '@/domain/Antibiogram';
export const org = (name) => new OrganismValue(name);
export const abx = (name, route) => new SingleAntibioticValue(name, route);
export const value = (value) => new SensitivityValue(value);
export const route = (name) => {
    if (name === 'PO')
        return Routes.PO;
    if (name === 'IV')
        return Routes.IV;
    if (name === 'IV/PO')
        return Routes.IV_PO;
    throw new Error('Unknown Route');
};
export const iso = (value) => {
    return new IntegerNumberOfIsolates(+value);
};
export const infoItem = (value) => {
    if (value === 'inpatient')
        return Settings.INPATIENT;
    if (value === 'outpatient')
        return Settings.OUTPATIENT;
    if (value === 'urine')
        return Sources.URINE;
    if (value === 'non-urine')
        return Sources.NONURINE;
    throw new Error('Unknown Sample Info Item' + value);
};
export const info = (listStr) => {
    const list = listStr.split(/, ?/g).map((l) => infoItem(l));
    return new SampleInfo(list);
};
export const gram = (value) => {
    if (value === 'positive')
        return GramValues.POSITIVE;
    if (value === 'negative')
        return GramValues.NEGATIVE;
    if (value === 'unspecified')
        return GramValues.UNSPECIFIED;
    throw new Error('Unknown Gram Value ' + value);
};
export const place = (region, institution) => new Place(region, institution);
export const interval = (start, stop) => {
    const startYear = +start.slice(0, 4);
    const startMonth = +start.slice(4, 6);
    const endYear = +stop.slice(0, 4);
    const endMonth = +stop.slice(4, 6);
    return new Interval(new Date(+startYear, +startMonth - 1), new Date(+endYear, +endMonth - 1));
};
export const id = (id) => new AntibiogramId(id);
